<template>
  <div class="pixel">
    <div class="canvas">
      <h2 v-if="Utils.isPC()">像素鸟游戏</h2>
      <p v-if="Utils.isPC()">space(空格键)/鼠标左键 操作</p>
      <canvas
        ref="canvasDom"
        @touchstart.stop.prevent="playGameTouch"
        @click.stop.prevent="playGameClick"
      ></canvas>
    </div>
    <img ref="birdDom" :src="bird1" hidden />
    <img ref="headTopDom" :src="wallHead" hidden />
    <img ref="wallDom" :src="wallBody" hidden />
    <img ref="headBotDom" :src="wallBottom" hidden />
    <img ref="bgBigDom" :src="bg.bgBig" hidden />
    <img ref="startInfoDom" :src="startInfo.src" hidden />
  </div>
</template>

<script>
import config from "config/pixelBird.js";

const START_LEN = document.documentElement.clientWidth / 10;

export default {
  name: "pixel",
  data() {
    return {
      isStart: false,
      bird1: config.bird.src, // base64图片
      wallHead: config.wall.srcHead, // base64图片
      wallBody: config.wall.srcBody, // base64图片
      wallBottom: config.wall.srcBottom, // base64图片
      bg: config.bg, // base64图片
      startInfo: config.start, // base64图片
      isFly: false,
      wallList: [],
      diffcult: 10, // 每十分 增加一次难度
      score: 0,
      wallId: 1, // 添加的墙id
      birdInfo: config.bird, // 小鸟数据
      wallSpeed: config.wallSpeed, // 墙移动速度
      wallMargin: config.wallMargin, // 管道口 之间的间距
      nextWallMargin: config.nextWallMargin // 下一个管道间距离
    };
  },
  methods: {
    // 准备就绪
    ready() {
      if (!this.Utils.isPC()) {
        let clientW = document.documentElement.clientWidth;
        let clientH = document.documentElement.clientHeight;
        let clientDis = clientW >= clientH ? clientH : clientW;
        this.$refs.canvasDom.style.width = `${clientW}px`;
        this.$refs.canvasDom.style.height = `${clientH}px`;
      }
      this.start()
    },
    // 点击开始 移动端
    playGameTouch(ev) {
      let x = ev.targetTouches[0].clientX;
      let y = ev.targetTouches[0].clientY;
      if (!this.Utils.isPC()) this.click(x, y)
    },
    // 点击开始 web端
    playGameClick(ev) {
      let x = ev.offsetX;
      let y = ev.offsetY;
      if (this.Utils.isPC()) this.click(x, y)
    },
    click(x, y) {
      if (this.isStart) {
        this.isFly = true;
        return;
      }
      let w = this.canvas.clientWidth;
      let h = this.canvas.clientHeight;
      let width = this.startInfo.width;
      let height = this.startInfo.height;
      if (x >= (w - width) / 2 && x <= w / 2 + width / 2) {
        if (y >= (h - height) / 2 && y <= h / 2 + height / 2) {
          this.init();
        }
      }
    },
    // 开始场景
    start() {
      clearInterval(this.timer);
      this.initData();
      this.drawBg();
      this.drawStartBtn();
    },
    // 初始化 canvas 数据
    initData() {
      this.canvas = this.$refs.canvasDom;
      let canvas = this.$refs.canvasDom;
      this.ctx = canvas.getContext("2d");
      canvas.width = canvas.clientWidth;
      canvas.height = canvas.clientHeight;
      this.clearRect();
    },
    // 初始化游戏
    init() {
      clearInterval(this.timer);
      this.initData();
      this.addWall();
      this.drawWallList();
      this.play();
    },
    // 定时器
    play() {
      clearInterval(this.timer);
      this.isStart = true;
      this.timer = setInterval(() => {
        this.clearRect(false);
        this.drawBg();
        this.runWallList();
        this.drawWallList();
        this.drawBird();
        this.clearWall();
        this.gameOver();
        this.computeScore();
      }, config.time);
    },
    // 画图片
    createImg(imgData, dom, ctx = this.ctx) {
      ctx.drawImage(dom, imgData.x, imgData.y, imgData.width, imgData.height);
    },
    // 画开始按钮
    drawStartBtn() {
      let startBtn = {
        src: this.bg.start,
        x: (this.canvas.clientWidth - this.startInfo.width) / 2,
        y: (this.canvas.clientHeight - this.startInfo.height) / 2,
        width: this.startInfo.width,
        height: this.startInfo.height
      };
      this.isStart = false;
      this.createImg(startBtn, this.$refs.startInfoDom);
    },
    // 画背景
    drawBg() {
      let bgBig = {
        src: this.bg.bgBig,
        x: 0,
        y: 0,
        width: this.canvas.clientWidth,
        height: this.canvas.clientHeight
      };
      this.createImg(bgBig, this.$refs.bgBigDom);
    },
    // 画鸟
    drawBird(type = 0, ctx = this.ctx) {
      this.fall(this.birdInfo);
    },
    // 画出所有wallList中 数据
    drawWallList() {
      this.wallList.forEach(item => {
        this.drawWall(item);
      });
    },
    // 画柱子
    drawWall(obj) {
      this.createImg(obj.wallTop, this.$refs.wallDom);
      this.createImg(obj.headTop, this.$refs.headBotDom);
      this.createImg(obj.headBot, this.$refs.headTopDom);
      this.createImg(obj.wallBot, this.$refs.wallDom);
    },
    // 页面写入得分
    drawText(ctx = this.ctx) {
      let w = this.canvas.clientWidth / 2;
      let maxW = Number(`${String(this.score).length * 15}`);
      ctx.font = "bold 50px 楷体";
      ctx.textAlign = "left";
      ctx.textBaseline = "top";
      ctx.fillStyle = "#fff";
      ctx.fillText(this.score, w - maxW / 2, 30, maxW);
    },
    // 清空超出的柱子
    clearWall() {
      this.wallList = this.wallList.filter(item => {
        return item.wallTop.x >= -item.wallTop.width;
      });
    },
    // 改变wallList中 数据
    runWallList() {
      let arr = this.wallList;
      this.wallList.forEach(item => {
        item.wallTop.x -= this.wallSpeed;
        item.headTop.x -= this.wallSpeed;
        item.headBot.x -= this.wallSpeed;
        item.wallBot.x -= this.wallSpeed;
      });
      let lastX = arr[arr.length - 1].wallTop.x;
      let startAddX =
        this.canvas.clientWidth - (config.wall.width + this.nextWallMargin);
      if (lastX <= startAddX) {
        this.wallId++;
        this.addWall();
      }
    },
    // 增加墙
    addWall() {
      let margin = this.wallMargin; // 柱子间间隔
      let clientW = this.canvas.clientWidth; // 画布宽度
      let clientH = this.canvas.clientHeight; // 画布高度
      // 随机起始点保证间隔不变 高度不同 上边墙到random位置终 下边墙 random + margin 始
      let randomY = Math.floor((clientH - margin) * Math.random());
      randomY = randomY <= 50 ? randomY + 50 * Math.random() : randomY;
      randomY =
        clientH - (randomY + margin) <= 50
          ? randomY - 50 * Math.random()
          : randomY;
      if (this.wallList.length) {
        let arr = this.wallList;
        // 就算最后一个跟柱子的高度 判定下一个柱子的高度
        let prevWallH =
          arr[arr.length - 1].headTop.y + arr[arr.length - 1].headTop.height;
        // 根据横向速度和距离 计算所需时间
        let time = this.nextWallMargin / config.bird.vx;
        // 根据 h=1/2gt² 算出小鸟正常下落高度
        let fallHeight = 0.5 * config.bird.g * time * time;
        if (randomY >= prevWallH + fallHeight)
          randomY = prevWallH + fallHeight - 50;
      }
      // 管道墙 顶部/底部 突出部分 x轴 起始位置
      let HeadX = this.canvas.clientWidth;
      // 管道墙 管身 x轴起始位置
      let bodyX = this.canvas.clientWidth;
      // 生成数据
      let obj = {};
      obj.headW = config.wall.width;
      obj.headH = config.wall.height;
      obj.bodyW = config.wall.bodyW;
      obj.headX = clientW;
      obj.bodyX = clientW + 1;
      obj.topHeadY = randomY - obj.headH;
      obj.topBodyHeight = obj.topHeadY;
      obj.botHeadY = randomY + margin;
      obj.botBodyY = obj.botHeadY + obj.headH;
      obj.botBodyHeight = clientH;
      let out = {
        id: this.wallId,
        wallTop: {
          x: obj.bodyX,
          y: 0,
          width: obj.bodyW,
          height: obj.topBodyHeight,
          dom: "wallDom"
        },
        headTop: {
          x: obj.headX,
          y: obj.topHeadY,
          width: obj.headW,
          height: obj.headH,
          dom: "headTopDom"
        },
        headBot: {
          x: obj.headX,
          y: obj.botHeadY,
          width: obj.headW,
          height: obj.headH,
          dom: "wallDom"
        },
        wallBot: {
          x: obj.bodyX,
          y: obj.botBodyY,
          width: obj.bodyW,
          height: obj.botBodyHeight,
          dom: "wallDom"
        }
      };
      this.wallList.push(out);
    },
    // 下落
    fall(obj, isFly = this.isFly) {
      obj.x += obj.vx;
      if (obj.x < START_LEN) obj.y = this.canvas.clientHeight / 2;
      // obj.vy += obj.g;
      if (obj.x >= START_LEN) {
        obj.x = START_LEN;
        obj.vx = 0;
        obj.y += obj.vy;
        obj.vy += obj.g;
      }
      if (isFly) {
        obj.vy = -config.flyHeight;
        this.isFly = false;
      }
      this.createImg(obj, this.$refs.birdDom);
    },
    // 飞起
    fly(ev) {
      if (ev.keyCode === 32) this.isFly = true;
    },
    // 撞击判定
    gameOver() {
      let clientH = this.canvas.clientHeight;
      let bird = this.birdInfo;
      if (bird.y >= clientH - bird.height) {
        clearInterval(this.timer);
        this.drawStartBtn();
        return;
      }
      if (bird.y <= 0) {
        clearInterval(this.timer);
        this.drawStartBtn();
        return;
      }
      let hitWall = this.wallList.some(item => {
        let wallX = item.wallTop.x;
        let headX = item.headTop.x;
        let headW = item.headTop.width;
        let disTopY = item.headTop.y + item.headTop.height;
        let disBotY = item.headBot.y;
        if (bird.x + bird.width >= headX && headX + headW > bird.x) {
          return bird.y < disTopY || bird.y > disBotY;
        }
      });
      if (hitWall) {
        clearInterval(this.timer);
        this.drawStartBtn();
      }
    },
    // 计算得分
    computeScore() {
      this.wallList.forEach(item => {
        let wallX = item.wallTop.x;
        let headX = item.headTop.x;
        let headW = item.headTop.width;
        let disTopY = item.headTop.y + item.headTop.height;
        let disBotY = item.headBot.y;
        if (headX + headW < this.birdInfo.x && this.score !== item.id) {
          this.score++;
          // 增加难度
          if (this.score && this.score % this.diffcult === 0) {
            this.wallSpeed += 0.1;
            this.wallMargin -= 1;
            this.nextWallMargin -= 1;
            if (this.wallSpeed >= 10) this.wallSpeed = 10;
            if (this.wallMargin <= 100) this.wallMargin = 100;
            if (this.nextWallMargin <= 180) this.nextWallMargin = 180;
          }
        }
      });
      this.drawText();
    },
    // 清空画布
    clearRect(isClearData = true) {
      if (isClearData) {
        this.isFly = false;
        this.wallList = [];
        this.diffcult = 10;
        this.score = 0;
        this.wallId = 1;
        this.birdInfo = this.$copy(config.bird);
        this.wallSpeed = this.$copy(config.wallSpeed);
        this.wallMargin = this.$copy(config.wallMargin);
        this.nextWallMargin = this.$copy(config.nextWallMargin);
      }
      let w = this.canvas.clientWidth;
      let h = this.canvas.clientHeight;
      this.ctx.clearRect(0, 0, w, h);
    }
  },
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.ready();
      }, 30)
    })
    window.addEventListener("keydown", this.fly, true);
  },
  destroyed() {
    clearInterval(this.timer);
    window.removeEventListener("keydown", this.fly, true);
  }
};
</script>

<style lang="less" scoped>
.pixel {
  > .canvas {
    width: fit-content;
    height: fit-content;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    text-align: center;
    > canvas {
      user-select: none;
      cursor: pointer;
      box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.7);
      width: 800px;
      height: 500px;
      background-color: #fff;
      opacity: 0.8;
    }
  }
}
</style>
